import React from 'react';
import GatsbyImage from 'gatsby-image';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';

const containerStyles = ({ theme }) => css`
  ${theme.mq.huge`
    display: flex;
  `};
`;

const Container = styled('div')`
  ${containerStyles}
`;

const headerContainerStyles = ({ theme }) => css`
  ${theme.mq.huge`
     width: 30%;
     margin-right: 10%;
  `};
`;

const HeaderContainer = styled('div')`
  ${headerContainerStyles}
`;

const headerStyles = ({ theme }) => css`
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 2.5rem;
  color: ${theme.colors.p600};

  ${theme.mq.huge`
    font-size: 3rem;
  `};
`;

const Header = styled('h1')`
  ${headerStyles}
`;

const titleStyles = ({ theme }) => css`
  margin-top: 0;
  color: ${theme.colors.p400};

  ${theme.mq.huge`
    font-size: 2rem;
  `};
`;

const Title = styled('h2')`
  ${titleStyles}
`;

const contentContainerStyles = ({ theme }) => css`
  ${theme.mq.huge`
    width: 60%;
  `};
`;

const ContentContainer = styled('div')`
  ${contentContainerStyles}
`;

const imageStyles = css`
  left: 0;
  top: 0;
  margin-bottom: 20px;
`;

const Image = styled(GatsbyImage)`
  ${imageStyles}
`;

const textStyles = ({ theme }) => css`
  color: ${theme.colors.textColor};
  ${theme.mq.huge`
     width: 80%;
  `};
`;

const Text = styled('p')`
  ${textStyles}
`;

function About({ title, subTitle, image, content }) {
  return (
    <Container>
      <HeaderContainer>
        <Header>{title}</Header>
        <Title>{subTitle}</Title>
      </HeaderContainer>
      <ContentContainer>
        <Image fluid={image} alt="Author image" />
        <Text>{content.content}</Text>
      </ContentContainer>
    </Container>
  );
}

About.propTypes = {
  title: PropTypes.string.isRequired,
  subTitle: PropTypes.string.isRequired,
  image: PropTypes.object.isRequired,
  content: PropTypes.object.isRequired,
};

export default About;
